import _ from 'lodash';import React, { Component } from 'react';import YTSearch from 'youtube-api-search';import SearchBar from './components/SearchBar';import VideoList from './components/VideoList';import VideoDetail from './components/VideoDetail';const API_KEY = 'AIzaSyA61CUKWRsD3Aj2FB4p-np8iel81K_eolA';class App extends Component {  constructor(props) {    super(props);    this.state = {      videos: [],      selectedVideo: null,    };    this.videoSearch('Web Applications');  }  videoSearch(term) {    // get a list of videos, mindsmash by default    YTSearch({ key: API_KEY, term: term }, (videos) => {      this.setState({        videos: videos,        selectedVideo: videos[0],      });    });  };  render() {    const videoSearch = _.debounce((term) => { this.videoSearch(term) }, 300);    return (      <div>        <SearchBar onSearchTermChange={videoSearch} />        <VideoDetail video={this.state.selectedVideo}/>        <VideoList          onVideoSelect={selectedVideo => this.setState({ selectedVideo })}          videos={this.state.videos} />      </div>    );  }};export default App;


